import { createReducer } from '@reduxjs/toolkit'
import {
	error,
	tings,
	subscribed,
  filterName,
  updating,
  deleting,
  shouldClose,
 } from "./actions"

export const tingsSlice = {
  error: null,
  subscribed: false,
  updating: false,
  tings: [],
  filterName: ``,
  deleting: false,
  shouldClose: false,
}

const tingsReducer = createReducer(tingsSlice, {

  [shouldClose]: (state, action) => {
    state.shouldClose = action.shouldClose
    return state
  },
  
  [deleting]: (state, action) => {
    state.deleting = action.deleting
    return state
  },
  
  [updating]: (state, action) => {
    state.updating = action.updating
    return state
  },
  
  [filterName]: (state, action) => {
    state.filterName = action.filterName
    return state
  },

  [tings]: (state, action) => {
    state.tings = action.tings
    return state
  },

  [subscribed]: (state, action) => {
    state.subscribed = action.subscribed
    return state
  },

  [error]: (state, action) => {
    state.error = action.error
    return state
  },
  
})

export { tingsReducer }
