import { createReducer } from '@reduxjs/toolkit'
import {
	error,
  newMessage,
} from "./actions"

export const p2tSlice = {
  error: null,
  newMessage: {
    message: ``,
    valid: false,
    sending: false,
  },
}

const p2tReducer = createReducer(p2tSlice, {

  [newMessage]: (state, action) => {
    state.newMessage  = {
      ...state.newMessage,
      ...action.newMessage
    }
    return state
  },

  [error]: (state, action) => {
    state.error = action.error
    return state
  },
  
})

export { p2tReducer }
