import { createAction } from '@reduxjs/toolkit'
import firebase from '@firebase/app'
import '@firebase/firestore'
import { 	
	getStore,
} from '../../'

export const error = createAction(`TINGS/ERROR`)
export const tings = createAction(`TINGS/TINGS`)
export const subscribed = createAction(`TINGS/SUBSCRIBED`)
export const deleting = createAction(`TINGS/DELETING`)
export const filterName = createAction(`TINGS/FILTER/NAME`)
export const updating = createAction(`TINGS/UPDATING`)
export const shouldClose = createAction(`TINGS/SHOULDCLOSE`)

export const subscribeTings = () => {
	const db = firebase.firestore()
	const store = getStore()
	const hostname = store.getState().app.hostname
	db.collection(`tings`)
		// .where( `hostname` , 'in', [ hostname ] )
	    .onSnapshot(function(snap) {
	        const tings = []
	        snap.forEach(function(doc) {
	            tings.push({
	            	id: doc.id,
	            	data: doc.data()
	            })
	        })
	        // console.log ('tings', tings)
	        store.dispatch({ type: `TINGS/TINGS`, tings })
	        store.dispatch({type: `TINGS/SUBSCRIBED`, subscribed: true })
	    })
	return true
}


export const resetshouldClose = () => {
	const store = getStore()
	store.dispatch({ type: `TINGS/SHOULDCLOSE`, shouldClose: false })
	return true
}

export const deleteTing = id => {
	const store = getStore()
	store.dispatch({ type: `TINGS/DELETING`, deleting: true })	
	const db = firebase.firestore()
	db.collection(`tings`).doc(id)
		.delete()
		.then( ( result ) => {
			store.dispatch({ type: `TINGS/DELETING`, deleting: false })
			store.dispatch({ type: `TINGS/SHOULDCLOSE`, shouldClose: true })
			return true
		})
		.catch( ( error ) => {
			console.log ('deleteTing error', error)
			store.dispatch({ type: `TINGS/ERROR`, error })
			store.dispatch({ type: `TINGS/DELETING`, deleting: false })
			return false
		})	
}


export const updateAttribute = (tingId, attribute, value) => {
	if ( !tingId ) return false
	const store = getStore()
	const db = firebase.firestore()
	store.dispatch({type: `TINGS/UPDATING`, updating: true })
	store.dispatch({type: `APP/BACKDROP`, backdrop: true })
	db.collection(`tings`).doc( tingId )
		.set({ [attribute]: value }, { merge: true })
		.then(function(response) {
			store.dispatch({type: `TINGS/UPDATING`, updating: false })
			store.dispatch({type: `APP/BACKDROP`, backdrop: false })
			return true
		})
		.catch(function(error) {
			console.log ('updateAttribute error', error)
			store.dispatch({type: `TINGS/UPDATING`, updating: false })
			store.dispatch({type: `APP/BACKDROP`, backdrop: false })
			return false
		})
	return true	
}



export const filterByName = name => {
	const store = getStore()
	store.dispatch({type: `TINGS/FILTER/NAME`, filterName: name })
	return true
}

export const clearFilterName = () => {
	const store = getStore()
	store.dispatch({type: `TINGS/FILTER/NAME`, filterName: `` })
	return true
}

export const selectTing = fingerprint => {
	const store = getStore()
	store.dispatch({type: `TINGS/SELECTED`, selected: fingerprint })
	return true
}

export const deselectTing = () => {
	const store = getStore()
	store.dispatch({type: `TINGS/SELECTED`, selected: null })
	store.dispatch({type: `TINGS/DELETING`, deleting: false })
	return true
}