import React from 'react'
import { useSelector } from 'react-redux'
import clsx from 'clsx'
import {
    makeStyles,
    useTheme,
    IconButton,
    Typography,
} from '@material-ui/core/'
import { Icon } from '../theme'

const useStyles = makeStyles(theme => ({
    bottom:{
        display: 'flex',
    },
    version: {
        // textAlign: 'center',
        paddingTop: theme.spacing(2),
        paddingLeft: theme.spacing(1.5),
    }
}))

export default function Bottom() {
	const classes = useStyles()
    const secondary = useTheme().palette.secondary.main
    // const faded = `rgba(1,1,1,0.2)`
    const appSlice = useSelector(state => state.app)
    const {
        pJSON,
    } = appSlice
	return	<div className={clsx(classes.bottom)}>
                
                <IconButton
                    onClick={(e) => {
                        e.preventDefault()
                        window.open(`https://listingslab.com/work/wordpress/plugins/push2talk-plugin/`, `_blank`)
                    }}>
                  <Icon icon={`blokey`} color={ secondary } />
                </IconButton>

                <IconButton
                    onClick={(e) => {
                        e.preventDefault()
                        window.open(`https://github.com/listingslab-software/listingslab/projects/1`, `_blank`)
                    }}>
                  <Icon icon={`github`} color={ secondary } />
                </IconButton>
                
                <Typography className={ clsx( classes.version ) }
                    style={{
                        color: secondary,
                    }}
                    variant={`body2`}>
                    { pJSON.version }
                </Typography>
                
  			</div>
}