import React from 'react'
import { useSelector } from 'react-redux'
import { 
	updateNewMessage,
	updateNewMessageValidity,
	sendNewMessage,
} from '../redux/p2t/actions'
import clsx from 'clsx'
import {
    makeStyles,
    Button,
    TextField,
    CardActions,
    CardContent,
    CircularProgress,
} from '@material-ui/core/'
import { Icon } from '../theme'

const useStyles = makeStyles(theme => ({
	newMessage: {
	},
	grow:{
		flexGrow: 1,
	},	
	field: {
		// minWidth: 340,
	},
	btnTxt:{
		marginLeft: theme.spacing(),
		marginRight: theme.spacing(),
	},
	sendBtn:{

		marginLeft: theme.spacing(),
		marginRight: theme.spacing(),
	},
}))

export default function NewMessage(props) {

	const classes = useStyles()
	const { tingId } = props
	const p2tSlice = useSelector(state => state.p2t)
	const {
		newMessage,
	} = p2tSlice
	const {
		message,
		valid,
		sending,
	} = newMessage

	if ( !tingId ) return null

	return	<div className={clsx(classes.card)} >
				
				<CardContent>
					<TextField
						disabled={ sending }
						autoFocus
						id={ `newMessage` }
						label={ `New message` }
						className={clsx(classes.field)}
						value={ message }
						// helperText={ prompt }
						fullWidth
						required
						multiline
						rows={ 3 }
						onChange={ (e) => {
							e.preventDefault()
							updateNewMessage(e.target.value)
							updateNewMessageValidity(e.target.value)
						}}
					/>
				</CardContent>
				<CardActions>
					<div className={ clsx(classes.grow) } />

					{ sending ? <CircularProgress /> : <Button
						disabled={ !valid }
						className={clsx(classes.sendBtn)}
						color={`secondary`}
						variant={valid ? `contained` : `text`}
						onClick={(e) => {
							e.preventDefault()
							sendNewMessage( tingId, message )
						}}>
						<span className={clsx(classes.btnTxt)}>
							Push2Talk
						</span>
						<Icon icon={`p2t`} color={valid ? `inherit` : `disabled`} />
					</Button> }

					
				</CardActions>
			</div>
}