import React from 'react'
import { useSelector } from 'react-redux'
import clsx from 'clsx'
import {
    makeStyles,
    CardContent,
} from '@material-ui/core/'
import {
    Tings, 
    Bottom,
    SimpleBackdrop,
} from './'
import { subscribeTings } from '../redux/tings/actions'

const useStyles = makeStyles(theme => ({
    appshell:{
        // margin: theme.spacing(4),
        background: 'none',
        border: 'none',
        boxShadow: 'none',
    },
    grow: {
        flexGrow: 1,
    },
    vSpace:{
        height: theme.spacing(2)
    },
    btnTxt: {
        marginRight: theme.spacing(),
        marginLeft: theme.spacing(),
    },
    tabs: {
        // marginTop: 100,
    },
    title:{
        display: 'flex',
        // padding: theme.spacing(2)
    },
}))

export default function Appshell() {

	const classes = useStyles()
    const tingsSlice = useSelector(state => state.tings)
    React.useEffect(() => {
        const { 
            // tings, 
            subscribed, 
            subscribing 
        } = tingsSlice
        // console.log ('tings', tings )
        if ( !subscribing && !subscribed ) {
            subscribeTings()
        }
    }, [ tingsSlice ])

	return	<div className={clsx(classes.appshell)}>
                <SimpleBackdrop />
                <CardContent>
               
                    <div className={clsx(classes.tabs)}>
                        <Tings />
                    </div>

                     <div className={clsx(classes.title)}>
                       <div className={clsx(classes.grow)} />
                           <Bottom />
                       <div className={clsx(classes.grow)} />
                    </div>
                    
                </CardContent>
  			</div>
}
