import React from 'react'
import clsx from 'clsx'
import { useSelector } from 'react-redux'
// import firebase from 'firebase'
// import firebase from 'firebase/app'
// import 'firebase/auth'
// import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth'
import {
    makeStyles,
    useTheme,
    Card,
    // Link,
    CardHeader,
    CardContent,
    Button,
    TextField,
    LinearProgress,
    Grid,
    Typography,
} from '@material-ui/core/'
import { Alert } from '@material-ui/lab/'
import { Icon } from '../theme'
import { signin } from '../redux/app/actions'

const useStyles = makeStyles(theme => ({
    authForm:{
    },
    link:{
        cursor: 'pointer',
    },
    contactLink:{
        marginTop: theme.spacing(2),
    },
    title:{
    },
    card:{
        padding: theme.spacing(),
        margin: theme.spacing(),
        border: 'none',
        boxShadow: 'none',
    },
    grow:{
        flexGrow: 1,
    },
    textField:{
        minWidth: 200,
    },
    btnTxt:{
    	marginRight: theme.spacing(),
        marginLeft: theme.spacing(),
    },
    vSpace:{
        height: theme.spacing(2)
    },
}))

export default function LoginForm() {

	const classes = useStyles()
    const secondary = useTheme().palette.secondary.main
	const [creds, setCreds] = React.useState({ 
                                email: null, 
                                password: null 
                              })
	const appSlice = useSelector(state => state.app)
    const {
    	error,
        isAuthing,
    } = appSlice

	const validate = () => {
        let isValid = true
        const email = document.getElementById(`email`).value
        const password = document.getElementById(`password`).value
        if (email === ``){
            isValid = false
        }
        if (password === ``){
            isValid = false
        }
        let result = {
            isValid,
            email,
            password,
        }
        setCreds({ 
            email: email.toLowerCase(), 
            password,
        })
        return result
    }
	



	return	<React.Fragment>
  				<div className={ clsx( classes.authForm )}>
                    

	                { !isAuthing ? <React.Fragment>


                        <Grid container>
                            <Grid item xs={12}>
                                
                                <Card className={ classes.card }>

                                    <CardHeader 
                                        disableTypography
                                        title={ <Typography variant={ `body1` }>
                                                    Listingslab WordPress Plugin
                                                </Typography> }
                                        // subheader={ <Typography variant={ `body2` }>
                                        //                 To an existing account
                                        //             </Typography> }
                                        avatar={ <Icon icon={`wordpress`} color={ secondary } /> }
                                    />
                                    
                                    <CardContent>

                                    { error ? <div>
                                        <Alert severity="warning">
                                            { error }
                                        </Alert>
                                        <div className={classes.vSpace} />
                                    </div> : null }
                                        
                                        <TextField
                                            fullWidth
                                            label={`Email`}
                                            className={classes.textField}
                                            id={`email`}
                                            size={`small`}
                                            onChange={(e) => {
                                                validate()
                                            }}/>

                                        <div className={classes.vSpace} />
                                        
                                        <TextField
                                            fullWidth
                                            label={`Password`}
                                            className={classes.textField}
                                            type={`password`}
                                            id={`password`}
                                            size={`small`}
                                            onChange={(e) => {
                                                validate()
                                            }}/>

                                        <div className={classes.vSpace} /> 
                                        <div className={classes.vSpace} /> 

                                        <Button
                                            fullWidth
                                            variant={`text`}
                                            color={ `primary` }
                                            onClick={(e) => {
                                                e.preventDefault()
                                                if (validate().isValid) {
                                                    signin(creds)
                                                }
                                            }}>
                                            <span className={classes.btnTxt}>
                                                Login
                                            </span>
                                            <Icon icon={`login`} color={ `inherit` } />
                                        </Button>
                                        
                                        
                                        
                                    </CardContent>
                                </Card>
                            </Grid>
                        </Grid>

                    	<div className={classes.vSpace} />
                	</React.Fragment> 
                	: <React.Fragment>
	                        		<div className={classes.vSpace} />
	                        		    <LinearProgress />
	                        		<div className={classes.vSpace} />
	                        	</React.Fragment>
                	}                    
                </div>
  			</React.Fragment>
}

