import pJSON from '../../../package.json'
import { createReducer } from '@reduxjs/toolkit'
import {
	error,
	authStateChanged,
	authed,
	isAuthing,
  user,
  backdrop,
} from "./actions"

export const appSlice = {
  pJSON,
  hostname: window.location.hostname,
  error: null,
  authStateChanged: false,
  authed: false,
  isAuthing: false,
  user: null,
  backdrop: false,
}

const appReducer = createReducer(appSlice, {

  [backdrop]: (state, action) => {
    state.backdrop = action.backdrop
    return state
  },

  [error]: (state, action) => {
    state.error = action.error
    return state
  },

  [user]: (state, action) => {
    state.user = action.user
    return state
  },
  
  [authStateChanged]: (state, action) => {
    state.authStateChanged = action.authStateChanged
    return state
  },

  [authed]: (state, action) => {
    state.authed = action.authed
    return state
  },

  [isAuthing]: (state, action) => {
    state.isAuthing = action.isAuthing
    return state
  },

  
})

export { appReducer }
