import * as React from 'react'
import clsx from 'clsx'
import { useSelector } from 'react-redux'
import { orderByEpoch } from '../lib'
import {
    makeStyles,
    Grid,
    CardMedia,
    Typography,
} from '@material-ui/core/'
import { 
  Ting,
} from './'

const useStyles = makeStyles((theme) => ({
  tings: {
  },
  media: {
      height: 0,
      paddingTop: '18.75%', // 56.25% 16:9
  },
  centered:{
    // border: '1px solid red',
    textAlign: 'center'
  },
  flag: {
    width: 28,
    height: 28,
  },
}))

export default function Tings() {

  const classes = useStyles()

  const tingsSlice = useSelector(state => state.tings)
  const { 
    tings,
  } = tingsSlice

  let mediaSrc = ``
  let assetPath = `svg/headers`
  if (window.bloginfo) assetPath = `${ window.bloginfo.wpurl }/wp-content/plugins/listingslab/public/svg/headers`
  mediaSrc = `${assetPath}/listingslab-plugin.svg`

  let tingList = []
  if (tings.length){
      for ( let i=0; i<tings.length; i++ ){
        tingList.push({ 
          id: tings[i].id,
          ...tings[i].data,
        })
      }
  }
  tingList = orderByEpoch( tingList, `updated` )
  
  return  <div className={ clsx( classes.tings ) }>
            <Grid container>
            
                    <Grid item xs={ 12 } >
                        <CardMedia 
                            className={clsx (classes.media) }
                            image={ mediaSrc }
                        />
                    </Grid>
                    <Grid item xs={ 12 } >
                        <Typography 
                          variant={`h2`} 
                          className={clsx (classes.centered) }>
                          Push2Talk
                        </Typography>
                    </Grid>
                    <Grid item xs={ 12 } className={ clsx( classes.centered ) }>
                      { !tings.length ? null : <React.Fragment>
                        { tingList.map(( item,i ) => {
                          return  <Ting 
                                    key={ `ting_${ i }` }
                                    tingData={ item } 
                                  />
                        })}
                      </React.Fragment>}
                    </Grid>
                </Grid>
          </div>
}
