import { createAction } from '@reduxjs/toolkit'
import firebase from '@firebase/app'
import '@firebase/firestore'
import { getStore } from '../../'

export const error = createAction(`P2T/ERROR`)
export const newMessage = createAction(`P2T/NEWMESSAGE`)


export const updateVisitor = (tingId, attribute, value) => {
	if ( !tingId ) return false
	const store = getStore()
	const db = firebase.firestore()
	store.dispatch({type: `TINGS/UPDATING`, updating: true })
	db.collection(`tings`).doc( tingId )
		.set({ [attribute]: value }, { merge: true })
		.then(function(response) {
			store.dispatch({type: `TINGS/UPDATING`, updating: false })
			return true
		})
		.catch(function(error) {
			console.log ('updateAttribute error', error)
			store.dispatch({type: `TINGS/UPDATING`, updating: false })
			return false
		})
	return true	
}

export const sendNewMessage = (tingId, message) => {
	let newMessage = {
		created: Date.now(),
		sender: `admin`,
		message,
	}
	const db = firebase.firestore()
	const store = getStore()
	store.dispatch({ type: `P2T/NEWMESSAGE`, newMessage: { sending: true } })	
	db.collection(`tings`).doc( tingId )
		.update({
		    messages: firebase.firestore.FieldValue.arrayUnion(newMessage)
		})
		.then( ( result ) => {
			store.dispatch({ type: `P2T/NEWMESSAGE`, newMessage: { sending: false } })
			updateNewMessage(``)
			updateNewMessageValidity(``)
			updateVisitor(tingId, `attention`, true)
			return true
		})
		.catch( ( error ) => {
			console.log ('sendNewMessage error', error)
			store.dispatch({ type: `P2T/ERROR`, error })
			store.dispatch({ type: `P2T/NEWMESSAGE`, newMessage: { sending: false } })
			return false
		})
	return true
}

export const updateNewMessage = message => {
	const store = getStore()
	store.dispatch({ type: `P2T/NEWMESSAGE`, newMessage: { message } })
	return true
}

export const updateNewMessageValidity = message => {
	let valid = true
	if ( message.length < 1 ) valid = false
	const store = getStore()
	store.dispatch({ type: `P2T/NEWMESSAGE`, newMessage: { valid } })
	return true
}