import { createAction } from '@reduxjs/toolkit'
import firebase from '@firebase/app'
import '@firebase/auth'
import { 
	getStore,
} from '../../'

export const error = createAction(`APP/ERROR`)
export const authStateChanged = createAction(`APP/AUTHSTATECHANGED`)
export const authed = createAction(`APP/AUTHED`)
export const isAuthing = createAction(`APP/AUTHING`)
export const user = createAction(`APP/USER`)
export const backdrop = createAction(`APP/BACKDROP`)

export const toggleBackdrop = bool => {
	const store = getStore()
	store.dispatch({type: `APP/BACKDROP`, backdrop: bool })
	return true
}

export const signin = creds => {
	const store = getStore()
	store.dispatch({type: `APP/AUTHING`, isAuthing: true })
	const { email, password } = creds
	firebase.auth()
		.signInWithEmailAndPassword( email, password)
		.then(function(result) {
			store.dispatch({type: `APP/AUTHING`, isAuthing: false })
		})
		.catch(function(error) {
			store.dispatch({type: `APP/AUTHING`, isAuthing: false })
			store.dispatch({type: `APP/ERROR`, error: error.message })
		})
}

export const changeAuth = user => {
	const store = getStore()
	store.dispatch({ type: `APP/AUTHSTATECHANGED`, authStateChanged: true })
	if (user){
		let displayName = user.providerData[0].displayName ? user.providerData[0].displayName : user.displayName
		if (!displayName){
			displayName = ``
		}
		let avatar = user.providerData[0].photoUrl ? user.providerData[0].photoUrl : user.photoUrl
		if (!avatar){
			avatar = `/svg/default`
		}
		let userData = {
	    	uid: user.uid,
	    	email: user.email,
	    	displayName,
	    	avatar,
	    	providerId: user.providerData[0].providerId,
	  	}
		store.dispatch({ type: `APP/AUTHED`, authed: true })
		store.dispatch({ type: `APP/USER`, user: userData })
	} else {
		store.dispatch({ type: `APP/AUTHED`, authed: false })
	}
}

export const signout = () => {
	const store = getStore()
	store.dispatch({type: `APP/AUTHING`, isAuthing: true })
	firebase.auth().signOut()
		.then(function(result) {
			store.dispatch({type: `APP/AUTHING`, isAuthing: false })
			store.dispatch({ type: `APP/AUTHED`, authed: false })
		})
}
