import React from 'react'
import clsx from 'clsx'
import {
	makeStyles,
	TextField,
	IconButton,
	Typography,
} from '@material-ui/core/'
import { updateAttribute } from '../redux/tings/actions'
import { Icon }from '../theme'

const useStyles = makeStyles(theme => ({
	field: {
		// margin: theme.spacing(),
		// marginBottom: theme.spacing(4),
	},
	flex: {
		display: 'flex',
	},
	editable:{
		paddingTop: theme.spacing(1),
		//paddingLeft: theme.spacing(),
	},
	uneditable:{
		// border: '1px solid red',
		paddingTop: theme.spacing(1.5),
		//paddingLeft: theme.spacing(),
	},
}))

export default function EditableString( props ) {

	const classes = useStyles()
	const { 
		options
	} = props
	const defaultOptions = {
		id: null,
		attribute: null,
		title: null,
		autoFocus: false,
		required: false,
		description: null,
		helperText: null,
		label: null,
		multiline: false,
		rows: 0,
		variant: `standard`,
		value: ``,
	}
	let field = {
		...defaultOptions,
		...options,
	}

	const [editing, setEditing] = React.useState( false )
	const [value, setValue] = React.useState( field.value )

	if (!editing) {
		return <div className={clsx( classes.field, classes.flex )}>
					
					<Typography 
						variant={`body1`} 
						className={clsx( classes.uneditable )} >
						{ value }
					</Typography>

					<IconButton
						onClick={ (e) => {
							setEditing( true )
						}}>
						<Icon icon={`edit`} color={ `secondary` } />
					</IconButton>
					
				</div>
	}

	return	<div className={clsx( classes.field, classes.flex )}>

				

				<div className={clsx( classes.editable )}>
					<TextField
						value={ value }
						label={ field.label }
						id={ field.id }
						required={ field.required }
						autoFocus={ field.autoFocus }
						variant={ field.variant }
						multiline={ field.multiline }
						rows={ field.rows }
						// helperText={ field.helperText }
						onChange={ (e) => {
							setValue( e.target.value )
						}}
					/>
				</div>

				<IconButton
					onClick={ (e) => {
						e.preventDefault()
						setEditing( false )
						updateAttribute ( field.tingId, field.attribute, value )
					}}>
					<Icon icon={`save`} color={ `secondary` } />
				</IconButton>
				
			</div>
}






