import React from 'react'
import { useSelector } from 'react-redux'
import { getAvatarPath } from '../lib'
import moment from 'moment'
import { 
    deleteTing,
    resetshouldClose,
} from '../redux/tings/actions'
import { ordinalSuffix } from '../lib'
import clsx from 'clsx'
import {
    makeStyles,
    useMediaQuery,
    Avatar,
    Badge,
    Typography,
    CardHeader,
    ButtonBase,
    Dialog,
    DialogTitle,
    DialogActions,
    DialogContent,
    IconButton,
    CircularProgress,
    Tooltip,
    List,
    ListItem,
    ListItemText,
    ListItemAvatar,
    TextField,
    Popover,
} from '@material-ui/core/'
import { 
    EditableString,
    // Location,
    // Device,
    NewMessage,
} from './'
import { Icon } from '../theme'

const useStyles = makeStyles(theme => ({
    ting:{
        display: 'block',
        textAlign: 'left',
        width: '100%',
        borderBottom: '1px solid rgba( 1, 1, 1, 0.025 )',
    },
    newMessage:{
        width: '100%',
    },
    autoFocus:{
        height: 0,
        overflow: 'hidden',
    },
    link: {
        cursor: 'pointer',
    },
    dialogTing:{
        zIndex: 199999,
    },
    avatarSmall:{
        width: 50,
        height: 50,
    },
    flex: {
        display: 'flex',
    },
    padded: {
        margin: theme.spacing(),
    },
    progress: {
        // width: 10,
        // height: 10,
    },
    dialogHeader:{
        padding: 0,
        marginTop: theme.spacing(6),
    },
    btnTxt:{
        marginRight: theme.spacing(),
        marginLeft: theme.spacing(),
    },
    dialogActions:{
        display: 'flex',
        textAlign: 'left',
    },
    dialogActionDiv:{
        margin: theme.spacing(1.5),
    },
    grow:{
        flexGrw: 1,
    },
    avatars:{
        display: 'flex',
    },
    urlLink:{
        display: 'flex',
        margin: theme.spacing(),
    },
    urlLinkTxt:{
        marginTop: theme.spacing(1.75),
        cursor: 'pointer',
    },
    popover:  {
        zIndex: 1234769,
    }
}))


export default function Ting( props ) {
    
    

	const classes = useStyles()
    const fullScreen = !useMediaQuery(theme => theme.breakpoints.up('sm'))
    const tingsSlice = useSelector(state => state.tings)
    const { 
      deleting,
      shouldClose,
    } = tingsSlice
    const [ open, setOpen ] = React.useState( false )
    const [ anchorEl, setAnchorEl ] = React.useState( null )

    const handlePopoverOpen = e => {
        setAnchorEl( e.currentTarget )
    }

    const handlePopoverClose = () => {
        setAnchorEl( null )
    }
    const popoverOpen = Boolean(anchorEl)

    React.useEffect(() => {
        if (shouldClose){
            setOpen( false )
            resetshouldClose()
        }
    }, [ shouldClose ])

    const { tingData } = props 
    if (!tingData) return null
    
    const {
        id,
        displayName,
        hits,
        hostname,
        ip,
        country,
        browser,
        browserVersion,
        url,
        stateProv,
        lat,
        lng,
        timeZone,
        localTime,
        deviceModel,
        deviceType,
        deviceVendor,
        wp_wpurl,
        wp_description,
        wp_title,
        updated,
        created,
        countryCode,
        avatar,
        engineVersion,
        currency,
        district,
        isEu,
        currencySymbol,
        osVersion,
        os,
        zipcode,
        currencyCode,
        fingerprint,
        city,
        platform,
        screenW,
        screenH,
        browserMajor,
        engine,
        languages,
        docTitle,
        messages,
        // userAgent,
    } = tingData


    const location = {
        ip,
        languages,
        lat,
        lng,
        city,
        stateProv,
        district,
        zipcode,
        country,
        countryCode,
        currency,
        currencySymbol,
        currencyCode,
        timeZone,
        isEu,
    }

    const device = {
        fingerprint,
        created,
        displayName,
        avatar,
        platform,
        os,
        osVersion,
        browser,
        browserVersion,
        browserMajor,
        engine,
        engineVersion,
        deviceModel,
        deviceType,
        deviceVendor,
        screenW,
        screenH,
    }
    const visit = {
        url: `${wp_wpurl}${url}`,
        updated,
        title: `${ docTitle }` ,
        site: `${ wp_title } ${ wp_description }` ,
        localTime,
        hits,
    }
    const show = false
    if (show){
        console.log (location, visit, device)
    }
    let assetPath = `svg/`
    if (window.bloginfo) assetPath = `${window.bloginfo.wpurl}/wp-content/plugins/listingslab/public/svg`
        var sortedMessages = []
    if (tingData) sortedMessages = tingData.messages
    let avatarSrc = `${ assetPath }/avatars/${ tingData.avatar }`
    let adminAvatarSrc = `${ assetPath }/avatars/owner.svg`

    return	<React.Fragment>
                <Dialog 
                    className={ clsx( classes.dialogTing ) }
                
                    open={ open }
                    fullScreen={ fullScreen }
                    fullWidth
                    onClose={(e) => {
                        e.preventDefault()
                        setOpen( false )
                    }}>
                    <DialogTitle>
                        <CardHeader 
                            className={ clsx( classes.dialogHeader ) }
                            avatar={ <React.Fragment>
                                        <IconButton
                                                color={ `secondary` }
                                                onClick={(e) => {
                                                    e.preventDefault()
                                                    console.log ('Change avatar')
                                            }}>

                                            <Avatar 
                                                src={ getAvatarPath(avatar) }
                                            />
                                        </IconButton>
                                    </React.Fragment> }
                            title={ <EditableString
                                            options={{
                                                tingId: id,
                                                attribute: `displayName`,
                                                value: displayName,
                                                autoFocus: true,
                                            }}
                                        /> }
                            action={ <React.Fragment>

                                        

                                        { deleting ? <CircularProgress className={ clsx( classes.progress ) }/> 
                                            : <Tooltip title={ `Delete ${id}` }>
                                                <IconButton
                                                    onClick={(e) => {
                                                        const confirmQuestion = `Really delete "${ displayName }"?`
                                                        e.preventDefault()
                                                        const confirmed = window.confirm(confirmQuestion)
                                                        if (confirmed){
                                                            deleteTing(id)
                                                        }
                                                    }}>
                                                    <Icon icon={`delete`} color={ `secondary` } />
                                                </IconButton>
                                            </Tooltip> }
                                        <IconButton
                                            onClick = { (e) => {
                                                e.preventDefault()
                                                setOpen( false )
                                                // toggleP2TDialog( false )
                                            }}>
                                            <Icon icon={`close`} color={`secondary`} />
                                        </IconButton>
                                </React.Fragment>}
                        />
                    </DialogTitle>
                    
                    <DialogContent>

                        { sortedMessages.length ? <List>
                            { sortedMessages.map((item, i) => {
                                const {
                                    sender,
                                    message,
                                    created,
                                } = item

                                let isLast = false
                                if (sortedMessages.length === i + 1){
                                    isLast = true
                                }



                                return <React.Fragment key={ `listitem_${i}` }>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar src={ sender === `visitor` ? 
                                                        avatarSrc : adminAvatarSrc } />
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={ message}
                                                    secondary={ moment(created).format('Do MMMM YYYY, h:mm a')} 
                                                />
                                            </ListItem>
                                        { isLast ? <div className={ clsx( classes.autoFocus ) } >
                                                            <TextField autoFocus />
                                                        </div> : null }
                                        </React.Fragment>
                            })}
                        </List> : null }

                        
                        
                    </DialogContent>


                    <DialogActions >
                        <div className={ clsx( classes.newMessage ) }>
                            <NewMessage tingId={ id } />
                        </div>
                    </DialogActions>


                    
                </Dialog>

                <ButtonBase 
                    className={clsx(classes.ting)}
                    aria-owns={ popoverOpen ? 'mouse-over-popover' : undefined}
                    aria-haspopup={ `true` }
                    onMouseEnter={ handlePopoverOpen }
                    onMouseLeave={ handlePopoverClose }
                    onClick={(e) => {
                        e.preventDefault()
                        setOpen( true )
                    }}>
                    <CardHeader 
                        disableTypography
                        avatar={ <div className={ clsx( classes.avatars ) }>
                                    <Badge badgeContent={ messages.length } color={ `secondary` }>
                                        <Avatar 
                                            className={clsx(classes.avatarSmall)}
                                            src={ getAvatarPath( avatar ) } 
                                        />
                                    </Badge>
                                </div>}
                        title={ <Typography variant={ `body1` }>
                                    <b>{ displayName }</b> { moment(updated).fromNow() } { ordinalSuffix( hits ) } hit
                                </Typography> }   
                        subheader={ <Typography variant={ `body2` }>
                                        { hostname }{ url }
                                    </Typography>}        
                    />
      			</ButtonBase>
                <Popover
                    id="mouse-over-popover"
                    className={classes.popover}
                    classes={{
                      paper: classes.paper,
                    }}
                    open={ popoverOpen }
                    anchorEl={ anchorEl }
                    anchorOrigin={{
                      vertical: 'bottom',
                      horizontal: 'left',
                    }}
                    transformOrigin={{
                      vertical: 'top',
                      horizontal: 'left',
                    }}
                    onClose={handlePopoverClose}
                    disableRestoreFocus
                  >
                    <Typography>I use Popover.</Typography>
                  </Popover>
        </React.Fragment>
}

