import React from 'react'
import { useSelector } from 'react-redux'
import clsx from 'clsx'
import {
  makeStyles,
  Paper,
  InputBase,
  // Divider,
  // IconButton,
} from '@material-ui/core/'
import { 
  filterByName,
} from '../redux/tings/actions'
// import { Icon } from '../theme'

const useStyles = makeStyles((theme) => ({
  searchBox: {
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
    width: 250,
    border: 'none',
    background: 'none',
    boxShadow: 'none',
    margin: theme.spacing(2),
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
  },
  iconButton: {
    padding: 10,
    marginLeft: theme.spacing(2),
  },
  divider: {
    height: 28,
    margin: 4,
  },
}))

export default function FilterName() {
  const classes = useStyles()
  const tingsSlice = useSelector(state => state.tings)
  const {
    filterName,
  } = tingsSlice

  return <Paper component="form" className={ clsx( classes.searchBox ) }>
          
          <InputBase
            // autoFocus
            value={ filterName }
            className={ clsx( classes.input ) }
            placeholder={ `Search` }
            onChange={ (e) => {
              e.preventDefault()
              filterByName (e.target.value)
            }}/>

        </Paper>
}


